@import url(https://fonts.googleapis.com/css?family=Francois+One|Gupter&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
}

body {
  font-family: 'Gupter', serif;
}

html, body {
  height: 100%;
  margin: 0;
  background-color: #F7DEBF;
}
.header {
  background-image: url(/restaurant-page-react/static/media/header.fcecf507.jpeg);
  background-position: -300px 40%;
  padding: 24vh 0 5vh 0;
  width: 100%;
  font-size: 400%;
  box-shadow: 0 0 13px black;
}

.header>.title {
  margin-left: 13vw;
}

@media only screen and (max-width: 600px) {
  .header {
    padding: 12vh 0 3vh 0;
  }
}
.title {
  font-family: 'Francois One', sans-serif;
  color: #506770;
  text-shadow: 2px 2px black;
  font-weight: bold;
}
.body {
  background-color: #F7DEBF;
  width: 100%;
}
.sitemap {
  text-align: center;
  font-size: 200%;
  width: 80%;
  margin: 0 auto;
  margin-top: 13vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ref {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.ref:hover, .ref:focus {
  color: red;
}

#menuRef {
  margin-top: 7vh;
  margin-bottom: 12vh;
}
.close {
  margin-top: 4%;
  margin-left: 5%;
  background: black;
  color: white;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  padding: 1.25%;
  box-sizing: border-box;
  font-size: 150%;
}

.close:hover, .close:focus {
  background: red;
}
#menu-container {
  width: 75%;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.menu-grid {
  display: grid;
  width: 90%;
  grid-column-gap: 25%;
  margin-top: 3%;
}

li {
  list-style: none;
  font-size: 123%;
  margin-bottom: 3.2%;
}

.item {
  text-align: left;
  grid-column-start: 1;
}

.price {
  text-align: right;
  grid-column-start: 2;
}

h2 {
  font-size: 210%;
  margin: 0;
}
#about-us {
  font-size: 150%;
  text-align: center;
  margin: 0 auto;
  margin-top: 4%;
  width: 80%;
}

#about-us strong {
  color: #6D312E;
}
#footer {
  margin: 0 auto;
  margin-top: 4em;
  margin-bottom: 6px;
}

#credit {
  margin-bottom: 0;
  padding: 0.5em 2em;
  box-shadow: 3px 3px #506770;
  font-family: 'Francois One', sans-serif;
  background-color: black;
  color: white;
}

#credit a, #credit a:visited {
  text-decoration: none;
  color: #85D9D2;
}

#credit a:hover {
  color: #6A846D;
}
